interface ConfigEditorState {
  stream_to_host: boolean,
  buffer_size_kb: number|null,
  trace_duration_ms: number|null,
  atrace_categories: {[s: string]: boolean};
}

interface State {
  counter: number;
  fragment: string;
  config_editor_state: ConfigEditorState;
  config: string | null;
}

function createZeroState(): State {
  return {
    counter: 0,
    fragment: "/control",
    config_editor_state: {
      stream_to_host: false,
      buffer_size_kb: null,
      trace_duration_ms: null,
      atrace_categories: {},
    },
    config: "echo 'Create a config above'",
  };
}

export {
  createZeroState,
  ConfigEditorState,
  State,
};

