import { State, ConfigEditorState, createZeroState } from '../backend/state';
import * as m from 'mithril';
import * as Atrace from  '../backend/atrace';

let gState: State = createZeroState();
let gDispatch: (msg: any) => void = _ => {};

function q(f: (e: any) => void): (e: any) => void {
  return function(e) {
    e.redraw = false;
    f(e);
  };
}

function incrementCounter() {
  return {
    topic: 'inc',
  };
}

function navigate(fragment: string) {
  return {
    topic: 'navigate',
    fragment,
  };
}

function setStreamToHost(enabled: boolean) {
  return {
    topic: 'set_stream_to_host',
    enabled,
  };
}

function setCategory(category: string, enabled: boolean) {
  return {
    topic: 'set_category',
    category,
    enabled,
  };
}

function setTraceDuration(duration_s: number) {
  return {
    topic: 'set_trace_duration',
    duration_s,
  };
}

function setBufferSize(buffer_size_mb: number) {
  return {
    topic: 'set_buffer_size',
    buffer_size_mb,
  };
}

const Menu: m.Component<{ title: string }> = {
  view(vnode) {
    return m("#menu",
      m('h1', vnode.attrs.title),
    );
  },
};

const Side = {
  view: function() {
    return m("#side",
      m('#masthead',
        m("img#logo[src='logo.png'][width=384px][height=384px]"),
        m("h1", "Perfetto"),
      ),
      m('ul.items', 
        m('li', { onclick: q(_ => gDispatch(navigate('/control'))) }, 'Home'),
        m('li', { onclick: q(_ => gDispatch(navigate('/config'))) }, 'Config Editor'),
      ),
    );
  },
};

const ControlPage = {
  view: function() {
    return [
      m(Menu, { title: "Control" }),
      m(Side),
      m('#content', 
        m("h1", {class: "title"}, `Counter: ${gState.counter}`),
        m("button", {
          onclick: q(_ => gDispatch(incrementCounter())),
        }, "A button"),
      ),
    ];
  },
};

async function copy(text: string|null) {
  if (!text)
    return;
  try {
    await (navigator as any).clipboard.writeText(text);
  } catch (err) {
    console.error('Failed to copy: ', err);
  }
}

const ConfigPage = {
  view: function() {
    return [
      m(Menu, { title: "Config Editor" }),
      m(Side),
      m('#content',
        m('.group', 'Trace Config'),
        m('label', m('.checkbox', m('input[type=checkbox]', {
            checked: gState.config_editor_state.stream_to_host,
            onchange: q(m.withAttr('checked', c => gDispatch(setStreamToHost(c)))),
        }), 'Stream to host')),
        m('label', m('input[type=number][min=0]', {
            value: (gState.config_editor_state.trace_duration_ms || 0) / 1000,
            onchange: q(m.withAttr('value', v => gDispatch(setTraceDuration(v)))),
        }), 'Trace duration (seconds)'),
        m('label', m('input[type=number][min=0]', {
            value: (gState.config_editor_state.buffer_size_kb || 0) / 1024,
            onchange: q(m.withAttr('value', v => gDispatch(setBufferSize(v)))),
        }), 'Buffer size (mb)'),
        Atrace.categories.map(category =>
          m('label', m('.checkbox', m('input[type=checkbox]', {
            checked: gState.config_editor_state.atrace_categories[category.tag],
            onchange: q(m.withAttr('checked', c => gDispatch(setCategory(category.tag, c)))),
          }), category.name)),
        ),
        gState.config && [
          m('code.block', gState.config),
          m('button', { onclick: () => copy(gState.config) }, 'Copy to clipboard'),
        ],
      ),
    ];
  },
};

function readState(): State {
  const config: ConfigEditorState|undefined = (m.route.param('config') as any);
  console.log(config);
  const state: State = createZeroState();
  if (m.route.get())
    state.fragment = m.route.get()
  if (config)
    state.config_editor_state = config;
  return state;
}

function updateState(new_state: State): void {
  const old_state = gState;
  gState = new_state;

  if (old_state.fragment == new_state.fragment) {
    //m.redraw();
    m.route.set(gState.fragment, {
      config: gState.config_editor_state,
    }, {
      replace: true,
      state: {
      }
    });
    return;
  }

  m.route.set(gState.fragment, {}, {
    replace: false,
    state: {
      config: gState.config_editor_state,
    }
  });
}

function main() {
  console.log('Hello from the main thread!');
  const worker = new Worker("worker_bundle.js");
  worker.onerror = e => {
    console.error(e);
  }
  worker.onmessage = msg => {
    switch (msg.data.topic) {
      case 'pong':
        console.log('Worker ACKed.');
        break;
      case 'new_state':
        updateState(msg.data.new_state);
        break;
    }
  };
  worker.postMessage({ topic: 'ping' });

  const root = document.querySelector('main');
  if (root == null) {
    console.error('No main element found.');
    return;
  }
  m.route(root, "/control", {
    "/control": ControlPage,
    "/config": ConfigPage,
  });

  gState = readState();
  m.redraw();

  gDispatch = worker.postMessage.bind(worker);
  console.log('initial:', gState);
  gDispatch({
    topic: 'init',
    initial_state: gState,
  });
}

export {
  main,
};
